import React , {Component} from 'react'
import avatar from '../images/avatar.jpg'
export default class HomeTitle extends Component {
    render(){
        return(
            <div className="section" id="homeTitle">
                <img className="img-circle" src={avatar}/>      
                <p>Hi I'm Shan</p>
            </div>
        )
    }
}


