import React, { Component } from 'react';
import './css/Index.css';
import HomeTitle from './component/HomeTitle.js';
import MyNav from './component/Navbar.js';
import MyTimeline from './component/MyTimeline.js';
class App extends Component {
  render() {
    return (
      <div id="index">
        <MyNav/> 
        <HomeTitle/>
        <MyTimeline/>
      </div>
    );
  }
}

export default App;



